<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto">
        <v-card-title class="justify-center">Buscar Procesos donde se manifestó Interés</v-card-title>
        <v-card-text>
            <v-row>
              <v-col cols="12">
                <span class="h2 font-weight-bold black--text">{{ `Estado: ${dataRegistro.estadotxt}` }}</span>
              </v-col>
            </v-row>
            <div v-if="dataRegistro.estado != 'R'">
              <br>
              <v-window>
                <v-window-item
                  key="card-1"
                >
                  <v-card
                    color="white"
                    elevation="2"
                  >
                    <v-row
                      class="fill-height"
                      align="center"
                      justify="center"
                    >
                      <v-col cols="12">
                        <span class="error--text">
                          Solo se podrá expresar interés en un proceso, si el estado actual de su cuenta es REGISTRADO.
                          Hasta que no tenga sus datos completos y acepte los términos y condiciones no podrá presentarse.
                          Solo se podrá consultar.
                        </span>
                      </v-col>
                    </v-row>
                  </v-card>
                </v-window-item>
              </v-window>
            </div>
            <FormBusqueda
              busEstado=""
              visEstado="1"
              @search="realizarConsulta">
            </FormBusqueda>
        </v-card-text>
      </v-card>
      <br>
      <v-card class="mx-auto">
        <v-card-title class="justify-center">Resultados de la Búsqueda</v-card-title>
        <v-card-text>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          >
          </v-text-field>
          <br>
          <v-data-table
            :headers="headers"
            :items="dataConsulta"
            :search="search"
            class="elevation-1"
          >
            <template v-slot:item.postulado="{ item }">
              <span>{{ ` ${item.postularse == 0 ? 'NO' : 'SI'} ` }}</span>
            </template>
            <!--<template v-slot:item.expinteres="{ item }">
              <v-icon v-if="item.proconregest == '' && dataRegistro.estado=='R'"
                class="mr-5"
                color="primary"
                @click="itemactual=item, dialog_expresarinteres=true"
              >
                thumb_up
              </v-icon>
            </template>
            <template v-slot:item.verdocs="{ item }">
              <v-icon v-if="dataRegistro.estado=='R'"
                class="mr-5"
                color="primary"
                @click="verDocumentos(item)"
              >
                view_list
              </v-icon>
            </template>-->
            <template v-slot:item.cargardocs="{ item }">
              <v-icon v-if="dataRegistro.estado=='R' && item.proconregest == 'I'"
                class="mr-5"
                color="primary"
                @click="abrirDialogoFiles(item)"
              >
                file_upload
              </v-icon>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-flex>

    <v-dialog
      v-model="dialog_expresarinteres"
      persistent
      max-width="600"
    >
      <v-card elevation="10">
        <v-card-title>
          <span class="headline justify-center red--text">Expresar Interes</span>
        </v-card-title>
        <v-card-text>
          ¿Está Seguro que desea expresar interés en el Proceso?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" text @click="expresarInteres">Aceptar</v-btn>
          <v-btn color="primary" dark text @click="itemactual='',dialog_expresarinteres=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog_infodocumentacion"
      persistent
      max-width="1000"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center blue--text">
          Documentación
        </v-card-title>
        <v-card-text>
          <v-tabs align-with-title v-model="tab">
            <v-tab key="P">Proceso</v-tab>
            <v-tab key="R">{{ itemactual.referencia + ' - ' + itemactual.titulo }}</v-tab>
            <v-tab key="C">Correspondencia</v-tab>
            <v-tab key="N">Novedades al Proceso</v-tab>
          </v-tabs>
          <v-tabs-items
            v-model="tab"
          >
            <v-tab-item key="P">
              <TabProc :itemactual.sync = "itemactual" />
            </v-tab-item>
            <v-tab-item key="R">
               <TabRef
                  :itemactual.sync = "itemactual"
                  :naturalezajuridica.sync = "dataRegistro.naturalezajuridica"
                  :correo.sync = "dataRegistro.email"
                />
            </v-tab-item>
            <v-tab-item key="C">
               <TabMsg :itemactual.sync = "itemactual" />
            </v-tab-item>
            <v-tab-item key="N">
               <TabNovedades :itemactual.sync = "itemactual" />
            </v-tab-item>
          </v-tabs-items>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="item='',itemactual='',dialog_infodocumentacion=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="dialog_loadfiles"
      persistent
      max-width="1200"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center blue--text">
          Cargar Documentación
        </v-card-title>
        <v-card-text>
          <loadFiles
            :idregistro.sync = "dataRegistro.codigo"
            :tipoContratacion.sync = "itemactual.tipo_contratacion"
            :naturalezajuridica.sync = "dataRegistro.naturalezajuridica"
            :id_termref.sync = "itemactual.id_termref"
            @cerrarDialogo="cerrarDialogoFiles"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="dialog_loadfiles=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
import FormBusqueda from '@/components/procesos/FormBusqueda'
export default {
  name: 'anuncios',
  props: ['codigo', 'estadotxt', 'estado'],
  components: {
    TabProc: () => import('./anunciosTabPro'),
    TabRef: () => import('./anunciosTabRef'),
    TabMsg: () => import('./anunciosTabMsg'),
    TabNovedades: () => import('./anunciosTabNovedades'),
    loadFiles: () => import('../cargararchivos/datosAdjuntos'),
    FormBusqueda
  },
  data () {
    return {
      dataRegistro: [],
      dataConsulta: [],
      search: '',
      itemactual: '',
      dialog_expresarinteres: false,
      dialog_infodocumentacion: false,
      tab: null,
      ultimaBusqueda: '',
      dialog_loadfiles: false,
      headers: [
        { text: 'Título del proceso', align: 'left', value: 'titulo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha límite', align: 'left', value: 'fecha_cierre_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Publicado', align: 'left', value: 'fecha_apertura_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Tipo de proceso', align: 'left', value: 'tipo_contratacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado', align: 'left', value: 'estado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Referencia', align: 'left', value: 'referencia', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Postulado', align: 'center', value: 'postulado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Cargar Docs.', align: 'center', value: 'cargardocs', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  methods: {
    async realizarConsulta (busqueda) {
      this.ultimaBusqueda = busqueda
      this.dataConsulta = []
      if (busqueda.busTitulo === '' && busqueda.busDescripcion === '' && busqueda.busReferencia === '' && busqueda.busFecPubIni === '' && busqueda.busFecLimFin === '' && busqueda.busTipoContratacion === '' && busqueda.busEstado === '') {
        this.$toasted.error('Debe ingresar al menos un criterio de búsqueda', { icon: 'error', duration: 5000 })
      } else {
        try {
          const { data } = await anunciosProcAdquisicion.buscarPropuesta({
            titulo: busqueda.busTitulo,
            descripcion: busqueda.busDescripcion,
            referencia: busqueda.busReferencia,
            fecha_apertura_ini: busqueda.busFecPubIni,
            fecha_apertura_fin: busqueda.busFecPubFin,
            fecha_cierre_ini: busqueda.busFecLimIni,
            fecha_cierre_fin: busqueda.busFecLimFin,
            estado: busqueda.busEstado,
            tipo_contratacion: busqueda.busTipoContratacion,
            estado_proceso: 'I',
            pubfreid: this.dataRegistro.codigo
          })
          this.dataConsulta = data
          if (this.dataConsulta.length === 0) {
            this.$toasted.error('No se encontraron resultados según los criterios de búsqueda', { icon: 'error', duration: 5000 })
          }
        } catch (error) {
          console.log(error)
        } finally {
        }
      }
    },
    async expresarInteres () {
      try {
        const { data } = await anunciosProcAdquisicion.expresarInteres({
          idpropuestaconvocatoria: this.itemactual.id,
          idregistro: this.dataRegistro.codigo,
          correo: this.dataRegistro.email,
          id_termref: this.itemactual.id_termref
        })
        if (data) {
          this.$toasted.success('Se actualizó el estado del Proceso', { icon: 'done', duration: 5000 })
          this.dialog_expresarinteres = false
          this.realizarConsulta(this.ultimaBusqueda)
        } else {
          this.$toasted.error('No se pudo actualizar el estado del Proceso', { icon: 'error', duration: 5000 })
        }
      } catch (error) {
        this.$toasted.error(error, { icon: 'error', duration: 5000 })
      } finally {
      }
    },
    verDocumentos (item) {
      this.itemactual = ''
      this.itemactual = item
      this.dialog_infodocumentacion = true
      this.tab = 'P'
    },
    abrirDialogoFiles (item) {
      this.itemactual = ''
      this.itemactual = item
      this.dialog_loadfiles = true
    },
    cerrarDialogoFiles () {
      this.itemactual = ''
      this.dialog_loadfiles = false
    }
  },
  watch: {
    '$store.state.dataRegistro': {
      handler: function (nv) {
        if (nv) {
          this.dataRegistro = nv
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
  }
}
</script>
